.main-page {
  display: flex;
  height: 982px;
  width: 100%;
  overflow: hidden;
}
.main-page section {
  width: 960px;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-page .mobile-responsive{
  display: none;
}

.main-page .images-pleace .main-img img {
  width: 300px;
  height: 350px;
  border-radius: 10px;
  cursor: pointer;
}

.main-page .images-pleace .poducts-imgs {
  width: 300px;
  margin: 10px 0px;
  display: flex;
  justify-content: space-between;
}
.main-page .images-pleace .poducts-imgs img {
  cursor: pointer;
  width: 70px;
  border-radius: 20px;
}
.main-page .text-and-price .show-users-about {
  width: 500px;
  display: flex;
  flex-direction: column;
}
.main-page .text-and-price .show-users-about .company-name {
  color: orange;
  font-size: 20px;
}
.main-page .text-and-price .show-users-about .company-ad {
  font-size: 35px;
  letter-spacing: 2px;
  word-spacing: 1px;
}
.main-page .text-and-price .show-users-about .offer-to-client {
  font-size: 20px;
}
.main-page .text-and-price .show-users-about .all-about-price {
  width: 180px;
}
.main-page
  .text-and-price
  .show-users-about
  .all-about-price
  .price-and-percent {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-page
  .text-and-price
  .show-users-about
  .all-about-price
  .price-and-percent
  p:first-child {
  font-size: 30px;
  font-weight: bolder;
}
.main-page .text-and-price .show-users-about .price-and-percent div {
  width: 40px;
  height: 20px;
  border-radius: 3px;
  background-color: rgb(233, 231, 227);
  color: orange;
  font-weight: 900;
  font-size: 17px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-page .text-and-price .show-users-about .all-about-price .previes-price {
  width: 90px;
  margin-top: -30px;
  font-size: 20px;
  color: rgba(8, 8, 8, 0.418);
  text-decoration: line-through;
}
.main-page
  .text-and-price
  .show-users-about
  .all-about-price
  .previes-price
  #usd-icon {
  color: rgba(8, 8, 8, 0.418);
  text-decoration: line-through;
}
.main-page .text-and-price .show-users-about .buy-product {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.main-page .text-and-price .show-users-about .buy-product .btn-quantity{
  width: 170px;
  display: flex;
  height: 40px;
  background-color: rgb(198, 204, 209);
  border-radius: 20px;
  justify-content: space-around;
  align-items: center;
}
.main-page .text-and-price .show-users-about .buy-product .minus,
.plus {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ff7e1b;
  background-color: rgb(204, 210, 214);
  font-size: 30px;
  cursor: pointer;
}
.main-page .text-and-price .show-users-about .buy-product .quantity {
  font-size: 25px;
}
.main-page .text-and-price .show-users-about .buy-product .add-to-cart {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 250px;
  height: 50px;
  background-color: #ff7e1b;
  border-radius: 10px;
  font-size: 20px;
}
.main-page .text-and-price .show-users-about .buy-product .add-to-cart p {
  color: white;
  text-align: left;
  width: 100px;
}
.main-page .text-and-price .show-users-about .buy-product .add-to-cart h3 {
  width: 60px;
  display: flex;
  align-items: center;
  font-size: 30px;
  color: aliceblue;
}

#usd-icon {
  font-size: 22px;
  color: black;
}
@media screen and (max-width: 800px) {
  .main-page .text-and-price .show-users-about{
    width: 400px;
  }
  .main-page .text-and-price .show-users-about .offer-to-client {
    width: 300px;
  }
}
@media screen and (max-width: 700px) {
  .main-page .text-and-price .show-users-about{
    width: 300px;
  }
  .main-page .text-and-price .show-users-about .buy-product {
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .main-page .text-and-price .show-users-about .buy-product .btn-quantity{
    width: 250px;
  }
  .main-page .images-pleace{
    width: 80%;
    margin: 0px 10px;
    display: flex;
    justify-content: center;
  }
  .main-page .images-pleace .main-img img {
    width: 250px;
    border-radius: 10px;
    cursor: pointer;
  }
  .main-page .images-pleace .poducts-imgs {
    width: 100px;
    display: flex;
  }
  .main-page .images-pleace .poducts-imgs{
    display: flex;
    justify-content: space-around;
  }
  .main-page .images-pleace .poducts-imgs img {
    cursor: pointer;
    width: 60px;
    border-radius: 20px;
    margin: 0;
  }
}
@media screen and (max-width: 600px) {
  .main-page{
    flex-direction: column;
    align-items: center;
  }
  .main-page .images-pleace{
    display: none;
  }
  .main-page .mobile-responsive{
    display: flex;
    width: 100%;
    height: 350px;
  }
  .main-page .mobile-responsive .everything-in{
    display: flex;
    align-items: center;
  }
  .main-page .mobile-responsive .everything-in .movement{
    z-index: 1;
    margin: 0px -50px;
     
  }
  .main-page .mobile-responsive .everything-in .movement button{
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  .main-page .mobile-responsive .everything-in img{
    width: 300px;
  }
  .main-page .images-pleace .main-img img {
    width: 270px;
    height: 270px;
    border-radius: 10px;
    cursor: pointer;
  }
  .main-page .images-pleace .poducts-imgs{
    display: none;
  }
  .main-page .text-and-price{
    height: 500px;
    width: 300px;
    justify-content: center;
    
  }
  .main-page .text-and-price .company-name{
    margin: 0px;
  }
  .main-page .text-and-price .show-users-about .all-about-price {
    width: 300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .main-page .text-and-price .show-users-about .all-about-price .previes-price {
    width: 90px;
    height: 40px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    font-size: 20px;
    color: rgba(8, 8, 8, 0.418);
    text-decoration: line-through;
  }
  .main-page .text-and-price .show-users-about .buy-product .btn-quantity{
    width: 100%;
    height: 45px;
  }
  .main-page .text-and-price .show-users-about .buy-product .add-to-cart{
    width: 100%;
  }
  @media screen and (max-height:982px) {
    .main-page .text-and-price{
      height: 400px;
    }
    .main-page .text-and-price .show-users-about .company-ad{
      margin-top: 5px;
    }
    .main-page .text-and-price .show-users-about .offer-to-client{
      margin-top: -25px;
    }
    .main-page .text-and-price .show-users-about .all-about-price{
      height: 40px;
    }
  }
  @media screen and (max-height:850px) {
    .main-page{
      overflow: hidden;
    }
    .main-page .mobile-responsive{
      
      margin: 30px 0px;
      height: 250px;
    }
  }
}
