* {
  outline: none;
  border: none;
  list-style: none;
  text-decoration: none;
  color: rgb(53, 48, 48);
}
.nav-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.472);
}
.nav-bar .IconAndNavs {
  width: 1536px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-bar .IconAndNavs .Links,.responsive-List {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.nav-bar .IconAndNavs .Links a,.responsive-List a{
  font-size: 20px;
  height: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.nav-bar .IconAndNavs .Links a:hover {
  border-bottom: 3px solid saddlebrown;
}
.nav-bar .private-pleace {
  width: 150px;
  height: 100%;
  display: flex;
}
.nav-bar .private-pleace li {
  align-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 100%;
  font-size: 27px;
}
.nav-bar .private-pleace li * {
  cursor: pointer;
}
.nav-bar .private-pleace .user-bouth{
  position:absolute;
  top:35px;
  margin-left: 15px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 15px;
  font-size: 15px;
  background-color: #FF7E1B;
  color: white;
}
.nav-bar .private-pleace li img {
  border-radius: 50%;
  border: 1px solid orange;
  align-self: flex-start;
  width: 45px;
  height: 45px;
}
.nav-bar .private-pleace .isnot-empty{
  width: 100%;
}
.nav-bar .private-pleace .isnot-empty .product-image{
  width: 70px;
  height: 70px;
  margin-top: 10px;
  border-radius: 5px;
  cursor: context-menu;
}
.nav-bar .private-pleace li div .isnot-empty div .ad-and-price{
  justify-content: center;
  align-items: center;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.657);
  
}
#usd-icon{
  font-size: 20px;
  color: rgba(0, 0, 0, 0.657);
}
.nav-bar .private-pleace li div .isnot-empty div .ad-and-price p{
  font-size: 18px;
  color: rgba(0, 0, 0, 0.657);
}
.nav-bar .private-pleace li div .isnot-empty .cart-main-structures{
  display: flex;
  justify-content: space-around;
  align-items: center;
  cursor: context-menu;

}
.nav-bar .private-pleace li div .isnot-empty .checkout-button-pleace{
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.nav-bar .private-pleace li div .isnot-empty .checkout-button-pleace .checkout-button{
  width: 80%;
  height: 50px;
  border-radius: 10px;
  background-color: #FF7E1B;
  color: white;
  font-size: 20px;
}
.Menu{
  display: none;
}
.close-nav-bar{
  display: none;
}
@media screen and (max-width:800px) {
  .nav-bar .IconAndNavs {
    justify-content: space-around;
  }
}
@media screen and (max-width:600px) {
  .nav-bar{
    justify-content: space-around;
  }
  .nav-bar .IconAndNavs {
    width: 200px;
    justify-content: space-around;
  }
  .nav-bar .IconAndNavs .Links{
    display: none;
  }
  .nav-bar .IconAndNavs .responsive-List{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 50%;
    height: 100%;
    z-index: 2;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  .nav-bar .IconAndNavs .responsive-List .close-nav-bar{
    display: block;
    position: absolute;
    top: 20px;
    left: 20px;
  }
  .nav-bar .IconAndNavs .responsive-List a{
    height: 60px;
  }
  .Menu{
    display: block;
  }
  *{
    cursor:context-menu;
  }
  /* @media screen and (max-height:850px) {
    .nav-bar{
      height: 60px;
    }
  } */
}